white;\n    font-weight: bold;\n    font-size: $l-size;\n    margin-bottom: $m-size;\n    width: 100%;\n    padding: 2.4rem;\n    -webkit-transition: .1s linear;\n    transition: .1s linear;\n    animation: shake 5s ease-in-out infinite alternate;\n\n}\n\n.big-button:disabled {\n    opacity: 0.5;\n}\n\n.big-button:hover{\n    -webkit-transform: rotate(360deg);\n    transform: scale(1.1);\n    animation: scale 1s ease-in-out ;\n\n    transition: 0.2s ease-in-out;\n    background: lighten($purple, 10%);\n\n}\n\n\n@keyframes scale {\n    0% {\n        -webkit-transform: scale(1);\n        transform: scale(1);\n    }\n\n    100% {\n         -webkit-transform: scale(1.1);\n         transform: scale(1.1);\n    }\n}\n\n@keyframes shake {\n    0% {\n        transform: translateX(0);\n        // -webkit-transform: scale(1.0);\n    }\n\n    25%{\n        transform: translateX(5px);\n\n    }\n    35%{\n        transform: translateX(-5px);\n    }\n\n    45%{\n        transform: translateX(0px);\n\n    }\n\n    100% {\n        transform: translateX(0px);\n        // -webkit-transform: scale(2.0);\n    }\n}\n\n\n// Button\n\n.button {\n    background: $purple;\n    border: none;\n    border-bottom: .3rem solid darken($purple, 10%);\n    color: white;\n    font-weight: 500;\n    padding: $s-size;\n}\n\n.button--link {\n    background: none;\n    border: none;\n    color: $off-white;\n    padding: 0;\n    min-width: 25%;\n    font-size: 1.6rem;\n}\n\n\n@media (min-width: $desktop-breakpoint){\n    .big-button{\n        margin-bottom: $xl-size;\n    }\n\n    .button--link{\n        min-width: 18%;\n        font-size: 2rem;\n    }\n}\n\n\n@import url(\"https://fonts.googleapis.com/css?family=Lato:400,100,300,700,100italic,300italic,400italic,700italic,900,900italic\");\n@import url(\"https://fonts.googleapis.com/css?family=Raleway:400,700\");\n\n\n// body .btn-bg.Ocean {\n//   background: #239ed3;\n// }\n\n\nbody .btn-bg.Ocean .btn-group .Coral button {\n  color: #fff;\n  overflow: h